.item{
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 20px;
    background-color: #fff;
    height: 70px;
    border-radius: 20px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.avatar{
    width: 50px;
    height: 50px;
}

.online{
    display: block;
    width: 20px;
    height: 20px;
    background-color: green;
    border-radius: 50%;
    margin-left: 20px;
    
}
.offline{
    display: block;
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 50%;
    margin-left: 20px;
}